import React from 'react';
const blank = 'https://res.cloudinary.com/ilya432/image/upload/v1579565634/like-blank_ltjvyu.png'
const liked = 'https://res.cloudinary.com/ilya432/image/upload/v1579565637/like-active_omljy8.png'

export default class PostAdd extends React.Component {

    state = { likeImgSrc: blank }

    onLikeClicked = (loggedInUser, post) => {
        console.log('post: ', post)
        if (this.state.likeImgSrc === blank) {
            this.setState({ likeImgSrc: liked })

            let likedBy = {
                _id: loggedInUser._id,
                nickName: loggedInUser.username,
                imgUrl: loggedInUser.imgUrl
            }

            this.props.likePost(likedBy, post)//
        }
        else {
            this.setState({ likeImgSrc: blank })
        }
    }

    render() {
        const { post, loggedInUser } = this.props
        return (
            <img className="post-btn like-btn" alt="like" title="like"
                onClick={() => this.onLikeClicked(loggedInUser, post)} src={this.state.likeImgSrc}>
            </img>
        );
    }
}