
import React from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router";
import Search from '../cmps/search';
import '../assets/css/Nav/Nav.css'

function Nav() {
    return <nav className="nav">
        <div className="nav-container flex row">
            <div className="logo-container nav-item flex row">
                <Link className="nav-link" to="/"><img className="Logo-img" alt="Logo" src={require('../assets/img/Logo.png')}></img></Link>
                <Link className="nav-link" to="/"><span className="logo-text" ><span className="logo-separator">|</span> Postagram</span></Link>
            </div>
            {/* <Link className="nav-item" to="/">User Reviews</Link> |
            <Link to="/about">Chat Room</Link> */}
            <Search />

            <div className="login-container nav-item">
                <Link className="nav-link" to="/login">
                    <img className="login-img" alt="login" src={require('../assets/img/login.png')}></img>
                </Link>
            </div>
        </div>

    </nav>
}
export default withRouter(Nav)