// React/Redux
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadReviews, addReview } from '../actions/ReviewActions.js';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts, likePost, addPost } from '../actions/PostActions.js';
// Style:
import '../../src/assets/css/index/index.css';
import '../../src/assets/css/Home/Home.css';

import PostAdd from '../cmps/PostAdd'
// Components
// import Feed from '../cmps/Feed'
import Like from '../cmps/Like'

class Home extends Component {
  state = {
    postToEdit: { _id: '', txt: '', aboutUserId: '', byUserId: '', username: '' },
    // posts: [], 
    reviews: [],
    reviewToEdit: { _id: '', txt: '', aboutUserId: '', byUserId: '', username: '' }
  };
  componentDidMount() {
    this.props.loadUsers();
    this.props.loadPosts();
    this.props.loadReviews();
  }

  handleChange = ev => {
    const { name, value } = ev.target;

    if (name === 'txt') {
      const addCommentBtn = document.querySelector('.add-comment-button')
      if (value === '') {
        addCommentBtn.classList.remove('highligt');
      } else {
        addCommentBtn.classList.add('highligt');
      }
    }
    this.setState(prevState => ({
      reviewToEdit: {
        ...prevState.reviewToEdit,
        [name]: value
      }
    }));
  };
  addReview = ev => {
    ev.preventDefault();
    this.props.addReview(this.state.reviewToEdit);
    this.setState({ reviewToEdit: { txt: '', aboutUserId: '' } });
  };

  countComments = (post) => {
    let commentsCount = post.commentedBy.length
    let result = ''
    if (commentsCount > 1) {
      result = 'show all ' + commentsCount + ' comments...';
    } else if (commentsCount === 0) {
      result = 'no comments'
    } else {
      result = 'show 1 comment...';
    }
    return result;
  }


  textAreaChange = ev => {
    const { name, value } = ev.target;
    this.setState(prevState => ({
      postToEdit: {
        ...prevState.postToEdit,
        [name]: value
      }
    }));
  };
  // addPost = (ev) => {
  //   ev.preventDefault();
  //   this.props.addPost(this.state.postToEdit);
  //   this.setState({ postToEdit: { txt: '', _id: '' } });
  // }
  // toggleLikePost = (post) => {
  //   alert('toggleLikePost -> outside')
  //   // e.preventDefault();
  //   var user = this.props.loggedInUser
  //   if (user) {
  //     // this.props.likePost(user, post)
  //   }
  //   // this.props.likePost(user, post)
  //   // var src = e.target.src;

  // }
  render() {
    const { posts } = this.props;
    return (
      <div className="home flex row">
        {/* <Feed /> */}
        {/* <h2>Posts</h2> */}
        <div className="home-container flex row">

          <section className="feed-container">
            {posts && <ul className="posts-list">
              {posts.map((post, i) => (
                <li key={post._id}>
                  <article className="post-container">
                    <div className="post-header">
                      <div className="post-header-img-container">
                        <Link className="" to={`/user/${post._id}`}><img className="post-header-user-img" alt="to user page" src={post.owner.imgUrl}></img></Link>
                        <span className="post-header-user-nickname">{post.owner.nickName}</span>
                      </div>
                    </div>
                    <div className="post-img-container">
                      <img className="post-img" alt="pic url" src={post.imgUrl}></img>
                    </div>




                    <div className="post-body-container">
                      <div className="post-buttons-container flex row">
                        {this.props.users && this.props.loggedInUser &&
                          <Like onLikeClicked={this.toggleLikePost} post={post} loggedInUser={this.props.loggedInUser} likePost={this.props.likePost} />}
                        <Link className="nav-link" to={`/about/${post._id}`}><img className="post-btn" alt="see post" title="see post" src={"https://res.cloudinary.com/ilya432/image/upload/v1579565617/comment_crvpjx.png"}></img></Link>
                        <img className="post-btn" alt="share" title="share" src={"https://res.cloudinary.com/ilya432/image/upload/v1579565517/share_kkrs2v.png"}></img>

                      </div>
                      <div className="post-body-data flex col">
                        <div className="post-body-data-section flex row">
                          <span className="post-body-user-nickname">{post.owner.nickName}</span>
                          <span className="post-body-user-txt">{post.txt}</span>
                          <span className="post-body-user-tags-span">{post.tags && <ul className="post-tags-list flex row">
                            {post.tags.map((tag, i) => (
                              <li key={i}>
                                <span className="post-header-user-tag">{'#' + tag}</span>
                              </li>
                            ))}
                          </ul>}</span>
                        </div>



                        <div className="post-body-data-section flex row">
                          {post.commentedBy &&
                            <Link className="nav-link" to={`/about/${post._id}`}><span className="post-show-all-comments"> {this.countComments(post)}</span></Link>}
                        </div>

                        <div className="flex row">
                          {this.props.reviews && <ul>
                            {this.props.reviews.map(review => (
                              <li key={review._id}>
                                <h3>{review.txt}</h3>
                                <p>
                                  <Link to={`user/${review.aboutUser._id}`}> About {review.aboutUser.username}</Link>
                                </p>
                                <p>
                                  <Link to={`user/${review.byUser._id}`}> By {review.byUser.username} </Link>
                                </p>
                              </li>
                            ))}
                          </ul>}


                          {this.props.users && this.props.loggedInUser &&
                            <form className="comment-container flex row" onSubmit={this.addReview}>
                              <select className="comment-select-user" onChange={this.handleChange} value={this.state.reviewToEdit.aboutUserId} name="aboutUserId">
                                <option value="">Select User</option>
                                {this.props.users.map(user => (<option key={user._id} value={user._id}> {user.username} </option>
                                ))}
                              </select>
                              <div className="add-comment-container">
                                <input className="add-comment-textarea" name="txt" type="text" onChange={this.handleChange} value={this.state.reviewToEdit.txt}></input>
                                <div className="add-comment-container-absolute">
                                  <span className="add-comment-button" onClick={this.addReview}>Post</span>
                                </div>
                              </div>
                            </form>}
                        </div>

                      </div>

                    </div>
                  </article>
                </li>
              ))}
            </ul>}
          </section>

          {this.props.users && this.props.loggedInUser && <section className="extras-container">
            <div className="extras flex col">
              <div className="user-image-container">
                <Link to={`user/${this.props.loggedInUser._id}`}>
                  <img className="user-image" alt="user pic" src={this.props.loggedInUser.imgUrl}></img>
                  <span className="user-username">{this.props.loggedInUser.username}</span>
                </Link>
              </div>
              <PostAdd addPost={this.props.addPost} loggedInUser={this.props.loggedInUser} props={this.props} />
            </div>
          </section>}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    reviews: state.review.reviews,
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    posts: state.post.posts
  };
};
const mapDispatchToProps = {
  loadUsers, loadPosts, likePost, addPost,
  addReview, loadReviews
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
